@import "https://fonts.googleapis.com/css2?family=Advent+Pro:wght@100;200;300;400;500;600;700&display=swap";
@import "https://fonts.googleapis.com/css2?family=Kanit:wght@100;200;300;400;500;600;700;800;900&display=swap";
:root {
  --color-primary: #f3442a;
  --color-dark: #111618;
  --color-light: #fafafa;
  --transition: all .5s ease-in-out;
  --border-rad-pill: 100px;
  --border-sm: 1px solid #fafafa1a;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background-color: #0000;
}

::-webkit-scrollbar-thumb {
  height: 10%;
  background-color: var(--color-primary);
  border-radius: var(--border-rad-pill);
}

::selection {
  background-color: var(--color-primary);
  color: var(--color-dark);
}

*, :before, :after {
  box-sizing: border-box;
  cursor: none;
  border: none;
  outline: none;
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
}

html {
  font-size: 62.5%;
  overflow-x: hidden;
}

body {
  width: 100%;
  letter-spacing: 1px;
  background-color: var(--color-dark);
  color: var(--color-light);
  font-family: Advent Pro, sans-serif;
  font-size: 2.5rem;
  font-weight: 400;
  line-height: 1.3;
  overflow-x: hidden;
}

.intro {
  z-index: 1010;
  width: 100%;
  height: 100vh;
  background-color: var(--color-primary);
  transition: all 1s;
  position: fixed;
  top: 0;
  left: 0;
}

.logo--header {
  color: var(--color-light);
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.intro--logo {
  opacity: 0;
  display: inline-block;
  position: relative;
  bottom: -20px;
}

.intro--logo.active {
  opacity: 1;
  transition: all .5s ease-in-out;
  bottom: 0;
}

.intro--logo.fade {
  opacity: 0;
  transition: all .5s ease-in-out;
  bottom: 150px;
}

.inner--cursor {
  z-index: 1000;
  width: 10px;
  height: 10px;
  background-color: var(--color-light);
  mix-blend-mode: difference;
  pointer-events: none;
  border-radius: 50%;
  transition: width .5s, height .5s;
  position: fixed;
  left: 10px;
  transform: translate(-50%, -50%);
}

.inner--cursor.grow {
  width: 25px;
  height: 25px;
  transition: width .5s, height .5s;
}

.outer--cursor {
  z-index: 1000;
  width: 25px;
  height: 25px;
  border: 1px solid var(--color-light);
  mix-blend-mode: difference;
  pointer-events: none;
  border-radius: 50%;
  transition: all .1s;
  position: fixed;
  left: 10px;
  transform: translate(-50%, -50%);
}

.wrapper {
  max-width: 80vw;
  margin: 0 auto;
}

.section--padding {
  border-bottom: var(--border-sm);
  padding: 20rem 0;
}

.section--title {
  margin-bottom: 10rem;
  font-size: 5rem;
  font-weight: 400;
}

.btn:link, .btn:visited {
  color: inherit;
  text-transform: uppercase;
  background-color: var(--color-primary);
  border-radius: var(--border-rad-pill);
  transition: var(--transition);
  padding: 1rem 3rem;
  font-weight: 500;
}

.btn:hover, .btn:active {
  background-color: var(--color-light);
  color: var(--color-dark);
}

img {
  width: 100%;
  display: block;
}

.navbar {
  height: 10rem;
  border-bottom: var(--border-sm);
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo h2 {
  font-size: 2.5rem;
  font-weight: 400;
}

.socials {
  z-index: 100;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  background-color: #0003;
  border-radius: 5px;
  flex-direction: column;
  gap: 1rem;
  padding: 2rem;
  display: flex;
  position: fixed;
  top: 30%;
  right: 2%;
}

.socials a {
  color: var(--color-light);
  font-size: 3rem;
  transition: all .3s;
}

.socials a:hover {
  color: var(--color-primary);
}

.footer.section--padding {
  text-align: center;
  padding: 10rem 0;
}

@media screen and (max-width: 768px) {
  html {
    font-size: 50%;
  }

  .wrapper {
    max-width: 90vw;
  }

  .section--padding {
    padding: 10rem 0;
  }

  .section--title {
    margin-bottom: 5rem;
  }
}

@media screen and (max-width: 475px) {
  .footer {
    font-size: 2rem;
  }
}

